import React, { useState, useEffect, useContext, useRef } from 'react';
import axios from 'axios';
import './form.component.scss';
import variables from '../../styles/_variables.module.scss';
import Sectionh2 from '../../components/sectionH2/sectionh2.component.jsx';
import Input from '../../components/input/input.component.jsx';
import Textarea from '../../components/textarea/textarea.component.jsx';
import Button from '../../components/button/button.component.jsx';
import ReCAPTCHA from 'react-google-recaptcha';
import { AppContext } from '../../AppContext';

export default function Form() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [sent, setSent] = useState(false);
  const [validateName, setValidateName] = useState(false);
  const [validateEmail, setValidateEmail] = useState(false);
  const [validateMessage, setValidateMessage] = useState(false);
  const [status, setStatus] = useState('SEND INQUIRY');
  const [captchaValue, setCaptchaValue] = useState();

  const { themeColorObj } = useContext(AppContext);
  const [themeColor, setThemeColor] = themeColorObj;

  const recaptchaRef = useRef();

  // const onChange = (value) => {
  //   setCaptchaValue(value);
  // };

  const mailformat =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  const checkEmailValidity = (e) => {
    e.match(mailformat) ? setValidateEmail(true) : setValidateEmail(false);
  }

  const checkNameValidity = (e) => {
    e !== '' ? setValidateName(true) : setValidateName(false)
  }

  const checkMessageValidity = (e) => {
    e !== '' ? setValidateMessage(true) : setValidateMessage(false)
  }

  const handleChange = (e) => {
    let stateName = e.target.name;
    let stateValue = e.target.value;
    if (stateName === 'name') {
      setName(stateValue);
      checkNameValidity(stateValue);
    }
    if (stateName === 'email') {
      setEmail(stateValue);
      checkEmailValidity(stateValue);
    }
    if (stateName === 'message') {
      setMessage(stateValue);
      checkMessageValidity(stateValue);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    // const token = captchaValue;
    // const token = recaptchaRef.current.getValue();
    // setCaptchaValue(token);
    const token = await recaptchaRef.current.executeAsync();
    setSent(true);
    setStatus('THANK YOU!');
    if(validateName && validateEmail && validateMessage) {
      try {
        const response = await axios.post('/contact', {
          name,
          email,
          message,
          token,
        });
        console.log('Request back', response);
      } catch (error) {
        console.error(error.code);
        console.error(error.message);
        console.error(error.stack);
      }
    }
    else {
      console.log("Form not valid. Please fill in all the fields!")
      return
    }

  };

  useEffect(() => {
    if (sent) {
      setTimeout(function () {
        setStatus('SEND INQUIRY');
        setName('');
        setEmail('');
        setMessage('');

        setValidateName(false);
        setValidateEmail(false);
        setValidateMessage(false);

        setSent(false);
      }, 3000);
    }
  }, [sent]);

  return (
    <form onSubmit={handleSubmit}>
      <Sectionh2 h2={'Send your inquiry'} />
      <Input
        label={'name'}
        type={'text'}
        placeholder={'Your name'}
        handleChange={handleChange}
        name={'name'}
        value={name}
      />
      <Input
        label={'email'}
        type={'email'}
        placeholder={'Your email address'}
        handleChange={handleChange}
        name={'email'}
        value={email}
      />
      <Textarea
        label={'message'}
        placeholder={'Please type your message'}
        handleChange={handleChange}
        name={'message'}
        value={message}
      />
      <div>
        <p className='small'>This site is protected by reCAPTCHA and the Google
          <a href="https://policies.google.com/privacy" style={{color:`${variables.secondaryColorDark}`}}> Privacy Policy</a> and
          <a href="https://policies.google.com/terms" style={{color:`${variables.secondaryColorDark}`}}> Terms of Service</a> apply.
        </p>
      </div>
      <Button type={'submit'} status={status}/>
      <ReCAPTCHA
        ref={recaptchaRef}
        sitekey={'6LeMJE4eAAAAAMTccweQVhjvYKhRjfAO9wrVFsQI'}
        // onChange={onChange}
        theme={themeColor === 'light' ? 'light' : 'dark'}
        size='invisible'
      />
    </form>
  );
}
